  background-color: rgb(255 255 255/30%);
}

.slider_home-slider_nav .w-slider-dot::after {
  content: "";
  width: 100%;
  height: 100%;
  background: white;
  transform: scaleX(0);
  transform-origin: left;
  transition: all 4s linear;
  display: block;
}

.slider_home-slider_nav .w-slider-dot.w-active::after {
  transform: scaleX(1);
}

