  transform: scaleX(0);
  transition: none;
}

.slider_home-slider_nav .w-slider-dot.w-active ~ .w-slider-dot::after {
  transform: scaleX(0);
  transition: none;
}

.slider_home-slider_nav .w-slider-dot:not(.w-active):not(.w-slider-dot.w-active ~ .w-slider-dot)::after {
  transform: scaleX(1);
  transition: none;
}

