  transform: scaleX(0);
  transition: transform 0.3s ease-in-out;
}

.pagination-button.active {
  transform: scaleX(1);
}

.pagination-button.active ~ .pagination-button {
  transform: scaleX(0);
}

/* Мгновенное изменение для предыдущих кнопок */
.pagination-button:not(.active):not(.pagination-button.active ~ .pagination-button) {
  transform: scaleX(1);
  transition: none;
}

/* Восстановление анимации для активной кнопки */
.pagination-button.active {
  transition: transform 0.3s ease-in-out;
}

