.slider_home-slider_nav .w-slider-dot {
	cursor: pointer;
	background-color: rgb(255 255 255 / 30%);
	width: 1em;
	height: 1em;
	margin: 0;
	/*transition: background-color .1s, color .1s;*/
	display: inline-block;
	position: relative;
	border-radius: 0;
	width: auto;
	height: 1px;
	flex: 1;
}

.slider_home-slider_nav .w-slider-dot.w-active {
	background-color: rgb(255 255 255 / 30%);
}

.slider_home-slider_nav .w-slider-dot::after {
	content: '';
	width: 100%;
	height: 100%;
	background: white;
	transform: scaleX(0);
	transform-origin: left;
	transition: all 4s linear;
	display: block;
}

.slider_home-slider_nav .w-slider-dot.w-active::after {
	transform: scaleX(1);
}

.slider_home-slider_nav .w-slider-dot.w-active[data-no-scale-x]::after {
	transform: scaleX(0);
	transition: none;
}

.slider_home-slider_nav .w-slider-dot.w-active ~ .w-slider-dot::after {
	transform: scaleX(0);
	transition: none;
}

.slider_home-slider_nav
	.w-slider-dot:not(.w-active):not(
		.w-slider-dot.w-active ~ .w-slider-dot
	)::after {
	transform: scaleX(1);
	transition: none;
}

.pagination-button {
	transform: scaleX(0);
	transition: transform 0.3s ease-in-out;
}

.pagination-button.active {
	transform: scaleX(1);
}

.pagination-button.active ~ .pagination-button {
	transform: scaleX(0);
}

/* Мгновенное изменение для предыдущих кнопок */
.pagination-button:not(.active):not(
		.pagination-button.active ~ .pagination-button
	) {
	transform: scaleX(1);
	transition: none;
}

/* Восстановление анимации для активной кнопки */
.pagination-button.active {
	transition: transform 0.3s ease-in-out;
}

.slider_home-slider_nav.w-round > div {
	border-radius: 0;
}

.slider_home-slider_nav.w-round .w-slider-dot:focus {
	outline: none;
	box-shadow: 0 0 0 0px #fff;
}

