.dropdown-container {
	position: relative;
	display: inline-flex;
	gap: 20rem;

	@media screen and (max-width: 991px) {
		display: contents;
	}
}

.dropdown-container:has(:checked) .more-button::after {
	content: '';
	margin-left: 4rem;
	width: 4rem;
	height: 4rem;
	background-color: var(--pink);
	border-radius: 50%;
}

.more-button {
	padding-block: 20rem;
	display: flex;
	align-items: center;
	color: #000000;
	cursor: pointer;
	border-radius: 25px;
	transition: all 0.3s ease;
	user-select: none;
	font-size: 12rem;
	line-height: 1;
	font-weight: 500;
	text-transform: uppercase;
}

.more-button svg {
	margin-left: 5px;
}


.dropdown-menu {
	border: 1rem solid #e9ecef;
	min-width: 200rem;
	position: absolute;
	top: 100%;
	left: 0;
	background: white;
	box-shadow: 0 4rem 6rem rgba(0, 0, 0, 0.1);
	z-index: 1000;
	opacity: 0;
	visibility: hidden;
	transform: translateY(-10rem);
	transition: all 0.3s ease;
}

.dropdown-container:hover .dropdown-menu {
	opacity: 1;
	visibility: visible;
	transform: translateY(0);
}

.dropdown-container svg {
	transition: all 0.3s ease;
}

.dropdown-container:hover svg {
	transform: rotate(180deg);
}

@media (max-width: 991px) {
	.more-button {
		display: none;
	}

	.dropdown-menu {
		position: relative;
		opacity: 1;
		visibility: visible;
		transform: none;
		padding: 0;
		border: none;
		box-shadow: none;
		display: contents;
	}
}


